<template>
  <div class="box">
    <Header>
      <div slot="center">购物车</div>
    </Header>
    <div class="content" v-if="flag">
      <ul>
        <li v-for="item of cartlist" :key="item.proid">
          <div class="cart-left">
            <!-- item.flag  得到数据的数据就添加了数据，默认给所有的都绑定 -->
            <van-checkbox v-model="item.flag"  checked-color="#07c160" @change="changeFlag(item)"></van-checkbox>
          </div>
          <div class="cart-center">
            <van-card
              :price="item.price"
              :title="item.proname"
              :thumb="item.proimg"
              />
            <div class="price">
                <van-stepper v-model="item.num" @change="reduce(item)"/>
                <i class=" delete iconfont icon-shanchu" @click='deleteItem(item,index)'></i>
            </div>
          </div>
        </li>
      </ul>
      <div class="submission">
        <van-submit-bar
        :price="totalPrice"
        :num="totalNum"
        button-text="去支付"
        @submit="onSubmit">
        <van-checkbox v-model="checkall" checked-color="#07c160" @click="selectAll">全选</van-checkbox>
        </van-submit-bar>
      </div>
    </div>
     <div class="noshop" v-else>
          <img src="http://img11.360buyimg.com/jdphoto/s180x180_jfs/t18163/292/540553659/74408/adeb7463/5a93c51cN3bb5e37b.png" alt="">
          <p class="kong">您的购物车为空请<span v-html="type"></span></p>
      </div>
  <router-view name="footer"></router-view>
  </div>
</template>

<script>
import axios from 'axios'
import Vue from 'vue'
import Header from '@/components/Header'
import { Card, Checkbox, CheckboxGroup, Stepper, SubmitBar, Toast } from 'vant'
Vue.use(Card)
Vue.use(Checkbox).use(CheckboxGroup)
Vue.use(Stepper)
Vue.use(SubmitBar)
export default {
  components: {
    Header
  },
  data () {
    return {
      checked: true,
      cartlist: [],
      value: true,
      num: '',
      flag: false, // 表示用户没有数据
      checkall: true,
      type: '',
      arr: '', // 用来保存选中的的信息
      proid: ''
    }
  },
  created () {
    // 获取用户得id，一个用户名一个产品
    let userid = localStorage.getItem('userid')
    let token = localStorage.getItem('token')
    let url = '/cart?userid=' + userid
    axios.get(url).then(res => {
      // console.log(res.data.code) // 初始请求数据，如果10119 说明没有登录，
      console.log(userid)
      console.log(res.data.data)
      if (!token) {
        // this.$router.push('/login')
        this.type = '<a href="#/login">登录</a>'
      } else if (res.data.code === '11000') {
        // 表示没有数据
        this.flag = false
        this.type = '<a href="#/home">购物</a>'
      } else {
        // 登录了，也有加入的数据
        this.flag = true
        let arr = res.data.data
        arr.map(item => {
          // 遍历数据，添加一个默认数据是直接选中的
          item.flag = true
        })
        this.cartlist = arr
        // console.log(this.cartlist)
      }
    })
  },
  computed: { // 计算属性
    totalPrice () {
      let price = 0
      this.cartlist.map((item) => {
        item.flag ? price += item.num * item.price * 100 : price += 0
      })
      return price
    },
    totalNum () {
      let num = 0
      this.cartlist.map((item) => {
        item.flag ? num += item.num : num += 0
      })
      return num
    }
  },
  methods: { // 事件
    reduce (item) {
      //  修改商品数量
      axios.get('/cart/update?cartid=' + item.cartid + '&num=' + item.num).then(res => {
        if (res.data.code === '10119') {
          this.$router.push('/login')
        } else {
          // 更新成功后 对象得属性值改变 会引起视图二次改变
          this.flag = true
        }
      })
    },
    deleteItem (item, index) {
      // console.log(item) 删除商品
      axios.get('/cart/delete?userid=' + item.userid + '&proid=' + item.proid).then(res => {
        if (res.data.code === '10112') {
          this.$router.push('/login')
        } else {
          // 删除截取
          this.cartlist.splice(index, 1)
        }
      })
    },
    // 单选框的点击事件来改变 全选
    changeFlag (item) {
      if (item.flag) {
        // 如果点击的这个为真了 说明该项是被选中的,判断当前是否点中了
        let b = this.cartlist.every(item => {
          // every 所有的都符合条件 返回一个判断条件
          return item.flag === true
        })
        // 如果b为真说明全部都选中了，有一个没有被选中的话 都为假的
        if (b) {
          this.checkall = true
        } else {
          this.checkall = false
        }
      } else {
        this.checkall = false
      }
    },
    selectAll () { // 全选的点击事件
      this.cartlist.map(item => {
        item.flag = !this.checkall
      })
    },
    onSubmit () {
      // console.log(this.totalPrice)
      // console.log(this.totalNum)
      this.arr = []
      // 遍历数组里面的为真的的添加
      this.cartlist.map(item => {
        if (item.flag) {
          this.arr.push(item)
        }
      })
      if (this.arr.length === 0) {
        // 如果没有选中商品
        Toast('请先添加商品')
        return
      }
      // console.log(this.arr)
      let tp = this.totalPrice / 100
      axios.get('/order/add?str=' + JSON.stringify(this.arr) + '&userid=' + localStorage.getItem('userid') + '&num=' + this.totalNum + '&price=' + tp).then(res => {
        // console.log(res.data.data)
        // console.log(res.data.data.orderid)
        // 提交订单成功 --- 跳转到订单页面 ---- 基础一定要传递 orderid
        this.$router.push('/order?orderid=' + res.data.data.orderid)
      })
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/lib/reset.scss';
.box{
  .content{
    @include rect(100%,100%);
    ul{
      @include rect(100%,100%);
      li{
        @include rect(100%,auto);@include flexbox();align-items:center;
          @include background-color(#fafafa);overflow: auto;
          border-bottom: 1px solid  #eee;
        // .cart-left{
        //   vertical-align: center;
        // }
        .cart-center{
          flex:1;margin-top:5px;position: relative;
          .price{
            position: absolute;right: 20px;bottom:15px;
              .delete{
              position: absolute;right: -53px; top:-25px;color: red;font-size:0.2rem;
            }
          }
        }
      }
    }
    .submission{
      @include rect(100%,auto);
      .van-submit-bar{
        bottom:0;
      }
    }
  }
  .noshop{
    @include rect(100%,100%);@include flexbox();flex-direction:column;align-items:center;
    justify-content:center;
    img{
      width:1.2rem;height: 1.2rem;
    }
    .kong{
      font-size: 0.18rem;margin-top: 5px;color: #000;
      span{
        color:red;font-weight: bold;margin-left:3px;
      }
    }
  }

}
.van-checkbox {
  padding-left:12px;
}
</style>
